import React, { useEffect, useState } from 'react';
import { Subject, timer } from 'rxjs'
import { format, min, max } from 'date-fns';
import { obsMetaTrader } from '../../../electron/bridges/main';
import { ICandles } from '../../../electron/ipc/main';


var subj = new Subject<obsMetaTrader>()
var m = format(new Date(), "yyyy-MM-dd hh:mm:ss")
const ReadRealTime: React.FC = () => {
  const [data, setData] = useState<ICandles[]>([])
  const [dataExcel, setDataExcel] = useState<any[]>([])
  const [showMessageOk, setShowMessageOk] = useState<boolean>(false)
  const [btnShow, setBtnShow] = useState<boolean>(false)
  const [padrao, setPadrao] = useState('Trend Down')

  useEffect(() => {
    document.body.style.backgroundColor = "rgb(243,243,243)"

    const time = timer(0, 500)
    time.subscribe(async () => {
      //var mtime = stats.mtime;
      const f = await window.electron.obsFileMetaTrader()
      const data = await window.electron.readExcel()
      setDataExcel(data)
      //const infoFile = f.info
      //if(format(new Date(m), "yyyy-MM-dd hh:mm:ss") !== format(infoFile.mtime, "yyyy-MM-dd hh:mm:ss")){
      if (true) {
        subj.next(f)
        //m = format(infoFile.mtime, "yyyy-MM-dd hh:mm:ss")
      }
    })

    subj.subscribe((e) => {
      setData(e.data)
    })
  }, [])

  const dataStart = () => {
    try {
      return format(min(data.map((e) => new Date(e.time))), 'dd/MM/yyyy HH:mm')
    } catch {
      return '-'
    }
  }

  const dataEnd = () => {
    try {
      return format(max(data.map((e) => new Date(e.time))), 'dd/MM/yyyy HH:mm')
    } catch {
      return '-'
    }
  }
  return (
    <div className='font-roboto'>
      <div onDoubleClick={() => window.electron.maximizeOnlyWindown()} className={`h-[32px] w-[2000px] absolute top-0 z-40 bg-gray-200 draggable`}></div>
      <div className='flex flex-row mt-10 ml-2'>
        <div className='p-5 shadow-md bg-white w-[670px] overflow-auto'>

          <table className='font-roboto text-sm mt-3'>
            <thead>
              <tr className='text-gray-900'>
                <th>Time</th>
                <th>Open</th>
                <th>Close</th>
                <th>High</th>
                <th>Low</th>
                <th>Ema 20</th>
                <th>Ema 08</th>
              </tr>
            </thead>
            <tbody>
              {
                data.map((e, index) => {
                  return (
                    <tr key={index}>
                      <td>
                        <div className='flex flex-row items-center bg-gray-100 mr-1text-gray-900'>
                          <div className='bg-indigo-500 w-2 h-[27px]'></div>
                          <span className='py-1 px-3'>{e.time}</span>
                        </div>
                      </td>
                      <td>
                        <div className='w-[70px] flex flex-row items-center bg-gray-100 mr-1text-gray-900'>
                          <span className='py-1 px-3'>{e.open.toFixed(0)}</span>
                        </div>
                      </td>
                      <td>
                        <div className='w-[70px] flex flex-row items-center bg-gray-100 mr-1text-gray-900'>
                          <span className='py-1 px-3'>{e.close.toFixed(0)}</span>
                        </div>
                      </td>
                      <td>
                        <div className='w-[70px] flex flex-row items-center bg-gray-100 mr-1text-gray-900'>
                          <span className='py-1 px-3'>{e.high.toFixed(0)}</span>
                        </div>
                      </td>
                      <td>
                        <div className='w-[70px] flex flex-row items-center bg-gray-100 mr-1text-gray-900'>
                          <span className='py-1 px-3'>{e.low.toFixed(0)}</span>
                        </div>
                      </td>

                      <td>
                        <div className='w-[70px] flex flex-row items-center bg-gray-100 mr-1text-gray-900'>
                          <span className='py-1 px-3'>{e.ema20.toFixed(0)}</span>
                        </div>
                      </td>

                      <td>
                        <div className='w-[70px] flex flex-row items-center bg-gray-100 mr-1text-gray-900'>
                          <span className='py-1 px-3'>{e.ema08.toFixed(0)}</span>
                        </div>
                      </td>

                    </tr>
                  )
                })
              }
            </tbody>
          </table>
        </div>
        <div className='p-5 shadow-md bg-white ml-2 w-[560px] h-[570px]'>
          <div className='flex'>
            <div className='flex-1 flex flex-col justify-center items-center shadow-md h-[130px] border-l-8 border-blue-500'>
              <div>
                {dataStart()}
              </div>
              <div className='text-sm text-gray-600'>
                Intervalo Inicio
              </div>
            </div>
            <div className='flex-1 flex flex-col shadow-md justify-center items-center h-[130px] ml-2 border-l-8 border-fuchsia-500'>
              <div>
                {dataEnd()}
              </div>
              <div className='text-sm text-gray-600'>
                Intervalo Fim
              </div>
            </div>
            <div className='flex-1 flex flex-col shadow-md justify-center items-center h-[130px] ml-2 border-l-8 border-x-cyan-500'>
              <div className='text-3xl'>
                {data.length}
              </div>
              <div className='text-sm text-gray-600'>
                Total de Candles
              </div>
            </div>
          </div>
          <div className='flex flex-col justify-center items-center h-[350px]'>
            {showMessageOk ? <>
              <span style={{ fontSize: '70px' }} className="material-symbols-outlined bg-green-200 rounded-full text-green-500">
                done
              </span>


              <div>
                Salvo com sucesso
              </div>
              <div className='text-sm text-gray-400 '>
                Padrão salvo no excel, caminho /Users/eriton/Documents/Estudo IA.xlsx
              </div>
            </> : <>

              {
                !btnShow ? (
                  <div>
                    <div className='flex flex-row items-center justify-center'>
                      <div className='h-4 w-4 mr-2 rounded-full bg-green-500 text-sm'></div>
                      <div className='text-gray-600 w-[120px]'>Trend High:</div> {dataExcel.filter(e => e.padrao === "Trend High").length}
                      <div className='ml-2'>| {(dataExcel.filter(e => e.padrao === "Trend High").length/dataExcel.length * 100).toFixed(2)}%</div>
                    </div>

                    <div className='flex flex-row items-center justify-center'>
                      <div className='h-4 w-4 mr-2 rounded-full bg-red-500 text-sm'></div>
                      <div className='text-gray-600 w-[120px]'>Trend Down:</div> {dataExcel.filter(e => e.padrao === "Trend Down").length}
                      <div className='ml-2'>| {(dataExcel.filter(e => e.padrao === "Trend Down").length/dataExcel.length * 100).toFixed(2)}%</div>
                    </div>

                    <div className='flex flex-row items-center justify-center'>
                      <div className='h-4 w-4 mr-2 rounded-full bg-purple-500 text-sm'></div>
                      <div className='text-gray-600 w-[120px]'>Range:</div> {dataExcel.filter(e => e.padrao === "Range").length}
                      <div className='ml-2'>| {(dataExcel.filter(e => e.padrao === "Range").length/dataExcel.length * 100).toFixed(2)}%</div>
                      
                    </div>
                      
                    <div className='flex flex-row items-center justify-center mt-5'>
                      <div className='text-gray-600 w-[120px]'>Total: </div><div>{dataExcel.length}</div>
                      </div>
                  </div>

                ) : <></>
              }

            </>
            }

          </div>
          <div className='flex justify-end items-center h-[65px]'>

            <div className='mr-5 flex flex-row items-center'>
              <span className='w-[250px] text-sm text-gray-500 text-right mr-2'>Escolher Padrão: </span>
              <select onChange={(e) => {
                setPadrao(e.target.value)
              }} className="block text-sm  appearance-none w-full bg-gray-200 border border-gray-200 text-gray-700 py-1 px-4 pr-8 rounded leading-tight focus:outline-none focus:bg-white focus:border-gray-500">
                <option value="Trend Down">Trend Down</option>
                <option value="Trend High">Trend High</option>
                <option value="Range">Range</option>
              </select>
            </div>


            <button onClick={() => {
              setBtnShow(true)
              let obj = {}
              for (let index = 0; index < data.length; index++) {
                if (index === 0) {
                  obj = {
                    ...obj,
                    time_range: dataStart() + "|" + dataEnd(),
                  }
                }
                const spread = data[index].close - data[index].open
                obj = {
                  ...obj,
                  ['spread_candle_' + Number(index + 1)]: spread,
                  ['spread_high_' + Number(index + 1)]: spread >= 0 ? data[index].high - data[index].close : data[index].high - data[index].open,
                  ['spread_low_' + Number(index + 1)]: spread >= 0 ? data[index].open - data[index].low : data[index].close - data[index].low,
                  ['spread_ema08_' + Number(index + 1)]: data[index].close - data[index].ema08,
                  ['spread_ema20_' + Number(index + 1)]: data[index].close - data[index].ema20
                }
              }
              obj = {
                ...obj,
                padrao: padrao
              }

              window.electron.sendWriteExcel(obj).then(() => {
                setTimeout(() => {
                  setShowMessageOk(true)
                }, 2000)
                setTimeout(() => {
                  setShowMessageOk(false)
                  setBtnShow(false)
                }, 5000);

              }).catch(() => {
                alert("Erro ao tentar salvar")
                setTimeout(() => {
                  setShowMessageOk(false)
                  setBtnShow(false)
                }, 3000);
              })
            }} disabled={btnShow || !(data.length === 11)} className={(showMessageOk || !(data.length === 11) ? 'bg-gray-200 text-gray-600' : 'bg-blue-600 text-white') + ('  text-sm rounded-md py-1 px-5')}> {btnShow ? 'Salvando...' : 'Salvar'}</button>
          </div>
        </div>
      </div>
    </div>
  )
}

export default ReadRealTime;