import React, { useEffect, useMemo, useRef, useState } from 'react';
import { ICandleLegacy, candles } from './data-candles';
import './index.css'
import { Subject, fromEvent, map } from 'rxjs';
import { format } from 'date-fns'
import ptBR from 'date-fns/locale/pt-BR';
import useDraggableScroll from 'use-draggable-scroll';


interface IPathScheme {
  data: string;
  fill: string;
  x: number;
  candle: ICandleLegacy
}

var pontoZero = 115213;
var pontoChartY = 1700;
var reduceY = 0.2
var reduceX = 0.05
var locGlobal = {
  x: 0,
  y: 0
}
var countDraw = 0
var locGlobalInicio = {
  x: 0,
  y: 0
}
var locGlobalEnd = {
  x: 0,
  y: 0
}
const Home: React.FC = () => {

  const [pathScheme, setPthScheme] = useState<IPathScheme[]>([])
  const [line, setLine] = useState(0)
  const [wUi, setWUi] = useState(0)
  const [hUi, setHUi] = useState(0)

  const [getDefault, setGetDefault] = useState(false)
  const [congelarScroll, setCongelarScroll] = useState(false)


  const [candleHover, setCandleHover] = useState<IPathScheme>({
    candle: {
      close: 0,
      date: '',
      high: 0,
      low: 0,
      media20: 0,
      open: 0
    },
    data: '',
    fill: '',
    x: 0
  })

  const ref = useRef<HTMLDivElement>() as React.MutableRefObject<HTMLInputElement>;
  const ref2 = useRef<HTMLDivElement>() as React.MutableRefObject<HTMLInputElement>;
  const ref3 = useRef<HTMLDivElement>() as React.MutableRefObject<HTMLInputElement>;

  const { onMouseDown } = useDraggableScroll(ref, { direction: 'both' });

  const obs = new Subject<{
    x: number
    y: number
  }>()

  obs.subscribe((e) => {
    reduceY = e.y
    reduceX = e.x
    updateSchemePath();
  })

  const getPricePixel = (price: number) => {
    return (pontoChartY + (price - pontoZero)) * reduceY
  };

  const onResize = fromEvent(window, "resize").pipe(map(() => {
    return {
      width: window.innerWidth,
      height: window.innerHeight
    }
  }));

  onResize.subscribe((e) => {
    setWUi(e.width)
    setHUi(e.height)
  })

  useEffect(() => {
    let v1 = 0
    let p: IPathScheme[] = []
    candles.forEach((e) => {
      var pathF = candleScheme(v1, e)
      p.push(pathF)
      v1 += 90
    })

    setPthScheme(p)
    setWUi(window.innerWidth)
    setHUi(window.innerHeight)

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])


  const updateSchemePath = () => {

    let v1 = 0
    let p: IPathScheme[] = []
    candles.forEach((e) => {
      var pathF = candleScheme(v1, e)
      p.push(pathF)
      v1 += 90
    })
    setPthScheme(p)
  }

  function candleScheme(x: number, candle: ICandleLegacy) {

    const isCandleBull = (candle.open - candle.close) < 0
    const spread = Math.abs(candle.close - candle.open)
    const spreadMaxBull = Math.abs(candle.close - candle.high)
    const spreadMaxBear = Math.abs(candle.open - candle.high)
    const spreadMinBull = Math.abs(candle.open - candle.low)
    const spreadMinBear = Math.abs(candle.close - candle.low)
    const spreadMax = isCandleBull ? spreadMaxBull : spreadMaxBear
    const spreadMin = isCandleBull ? spreadMinBull : spreadMinBear

    var path = {
      data: `m ${x * reduceX} ${(pontoChartY + (candle.high - pontoZero) * -1) * reduceY} v ${spreadMax * reduceY} m 0 0 h -${33 * reduceX} v ${spread * reduceY} h ${33 * reduceX} m 0 -${spread * reduceY} h ${33 * reduceX} v ${spread * reduceY} h -${33 * reduceX} m 0 0 l 0 ${spreadMin * reduceY}`,
      fill: isCandleBull ? '#00C898' : '#FF838A',
      x: x * reduceX,
      candle: candle
    }

    return path
  }

  const media = useMemo(() => {

    const els = pathScheme.map((e, index) => {
      if (index === 0) {
        return <></>
      } else {
        return <line
          key={index}
          x1={pathScheme[index - 1].x}
          x2={e.x}
          y1={(pontoChartY + (pathScheme[index - 1].candle.media20 - pontoZero) * -1) * reduceY}
          y2={(pontoChartY + (pathScheme[index].candle.media20 - pontoZero) * -1) * reduceY}
          className='hover:cursor-wait stroke-slate-500 hover:stroke-red-400'></line>
      }
    })
    return els
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [pathScheme])

  const data = useMemo(() => {
    const els = pathScheme.map((e, index) => {
      return <path key={index} onMouseEnter={() => {
        //console.log(e.candle)
      }} fill={e.fill} stroke="#676767" strokeWidth="1" d={e.data}></path>
    })
    return els
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [pathScheme])

  const sidebar = useMemo(() => {
    let sidebar: JSX.Element[] = []
    let price = 113200

    for (let index = 0; index < 100; index++) {

      const t = <text className='font-roboto text-sm' key={index} x="24" y={(getPricePixel(price) - 32)} fill='black'>{price.toFixed(0)}</text>

      sidebar.push(t)
      price += 250

    }
    return sidebar
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [pathScheme])

  const sidebarX = useMemo(() => {
    let sidebar: JSX.Element[] = []

    for (let index = 0; index < pathScheme.length; index++) {
      const d = new Date(pathScheme[index].candle.date)
      const data = format(d, "HH:mm")
      if (data === "09:00") {
        const t = <text key={index} className='text-sm fill-gray-700' x={pathScheme[index].x} y={23}>{format(d, "MMM dd", { locale: ptBR })}</text>
        sidebar.push(t)
      }

      if (data === "12:00") {
        const t = <text key={index} className='text-sm fill-gray-700' x={pathScheme[index].x} y={23}>{format(d, "H:mm", { locale: ptBR })}</text>
        sidebar.push(t)
      }

      if (data === "15:00") {
        const t = <text key={index} className='text-sm fill-gray-700' x={pathScheme[index].x} y={23}>{format(d, "H:mm", { locale: ptBR })}</text>
        sidebar.push(t)
      }

    }
    return sidebar
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [pathScheme])


  const lineMemo = useMemo(() => {
    return <line x1="0" y1={line} x2="25000" y2={line} stroke="black" />
  }, [line])

  const linePrice = useMemo(() => {
    return (
      <>
        <rect y={line - 45} height={30} width={100} fill='purple'></rect>
        <text stroke="white" x="10" y={line - 25}>{line}</text>
      </>
    )
  }, [line])

  return <>

    <div className='flex flex-col'>
      <div className={'flex flex-row relative'}>
        <div onDoubleClick={() => window.electron.maximizeOnlyWindown()} className={`h-[32px] w-[25000px] absolute top-0 z-40 backdrop-blur-lg bg-gray-700/10 draggable overflow-hidden`}></div>
        <div style={{ minHeight: (hUi - 64).toFixed(0) + "px", top: '32px' }} className={`select-none font-roboto flex flex-col justify-start pt-5 items-center z-40 absolute w-[45px] pb-4 backdrop-blur-lg bg-gray-700/10`}>
          <div onClick={() => {
            if(!getDefault){
              setCongelarScroll(!congelarScroll)
            }
         
          }} className={(congelarScroll ? 'bg-indigo-500 text-white' : 'bg-gray-300 text-gray-600') +' h-8 w-[38px] rounded-sm cursor-pointer flex justify-center items-center'}>
            <span className="material-symbols-outlined">
              drag_pan
            </span>
          </div>

          <div onClick={() => {
            setCongelarScroll(false)
            setGetDefault(!getDefault)
          }} className={(getDefault ? 'bg-indigo-500 text-white' : 'bg-gray-300 text-gray-600') +' h-8 w-[38px] rounded-sm cursor-pointer flex justify-center items-center mt-2'}>
            <span className="material-symbols-outlined">
              vital_signs
            </span>
          </div>

          <div onClick={() => {
          }} className='bg-gray-300 text-gray-600 h-8 w-[38px] rounded-sm cursor-pointer flex justify-center items-center mt-2'>
            <span>
              {countDraw}
            </span>
          </div>
        </div>

        <div className='hidden select-none font-roboto flex flex-col justify-center items-center absolute top-12 left-20 w-[300px] pb-4 rounded-md backdrop-blur-md bg-white/40 shadow-md'>
          <div>
            Y: {reduceY.toFixed(2)}
            <button onClick={() => {
              obs.next({
                x: reduceX,
                y: reduceY + 0.01
              })
            }} className='w-12 bg-purple-500 mx-2 text-white rounded-md'>+</button>
            <button onClick={() => {
              obs.next({
                x: reduceX,
                y: reduceY - 0.01
              })
            }} className='w-12 bg-purple-500 mx-2 text-white rounded-md'>-</button>
          </div>

          <div className='mt-5'>
            X:  {reduceX.toFixed(2)}
            <button onClick={() => {
              obs.next({
                x: reduceX + 0.01,
                y: reduceY
              })
            }} className='w-12 bg-purple-500 mx-2 text-white rounded-md'>+</button>
            <button onClick={() => {
              obs.next({
                x: reduceX - 0.01,
                y: reduceY
              })
            }} className='w-12 bg-purple-500 mx-2 text-white rounded-md'>-</button>
          </div>
          {ref.current !== undefined ?
            <div className='mt-5'>
              Scroll X:  {ref.current.scrollLeft}
              <button onClick={() => {

                if (ref.current.scrollLeft >= 0) {
                  ref.current.scrollLeft = ref.current.scrollLeft + 5
                }
                else {
                  ref.current.scrollLeft = 0
                }
              }} className='w-12 bg-purple-500 mx-2 text-white rounded-md'>+</button>
              <button onClick={() => {
                if (ref.current.scrollLeft > 0) {
                  ref.current.scrollLeft = ref.current.scrollLeft - 5
                }
                else {
                  ref.current.scrollLeft = 0
                }
              }} className='w-12 bg-purple-500 mx-2 text-white rounded-md'>-</button>
            </div> : <></>
          }


          <div className='mt-5'>
            <button onClick={() => setGetDefault(!getDefault)} className={getDefault ? 'bg-indigo-900 text-white px-3 rounded-md' :
              'bg-indigo-100 text-gray-500 px-3 rounded-md'}>Pegar padrão {countDraw}</button>
          </div>
        </div>

        <div onMouseDown={(e) => {
          if (congelarScroll) {
            onMouseDown(e)
          }

        }} onScroll={(e) => {
          const scrolLTop = ref.current.scrollTop
          const scrolLLeft = ref.current.scrollLeft
          ref2.current.scrollTop = scrolLTop
          ref3.current.scrollLeft = scrolLLeft
        }}
          ref={ref} className='bg-gradient-to-b from-white via-gray-100 to-gray-200 border shadow-md overflow-hidden cursor-default'
          style={{ maxWidth: wUi, maxHeight: hUi }}>
          <svg onClick={() => {
            countDraw++
            if (getDefault) {

              if (countDraw === 1) {
                console.log("Pasando por aqui")
                locGlobalInicio = {
                  x: locGlobal.x,
                  y: locGlobal.y
                }
              }
              if(countDraw >= 2) {
                setGetDefault(false)
              }
            } else {
              countDraw = 0
            }


          }} onMouseMove={(evt) => {
            //get position
            
            const svg: any = evt.currentTarget
            const pt = svg.createSVGPoint()
            pt.x = evt.clientX;
            pt.y = evt.clientY;
            locGlobal.y = pt.y
            locGlobal.x = pt.x

            const loc = pt.matrixTransform(svg.getScreenCTM().inverse());
            setLine(loc.y)
            if (countDraw === 1) {
              locGlobalEnd = {
                x: locGlobal.x,
                y: locGlobal.y
              }
            }
          }} width="25000" height="25000" fill="none" xmlnsXlink="http://www.w3.org/1999/xlink">
            <g>
              {/*  {lineMemo} */}
              {media}
              {data}
              {countDraw > 0 ? <rect
                y={(locGlobalEnd.y - locGlobalInicio.y) < 0 ? locGlobalInicio.y - ((locGlobalEnd.y - locGlobalInicio.y) * -1) : locGlobalInicio.y}
                x={(locGlobalEnd.x - locGlobalInicio.x) < 0 ? locGlobalInicio.x - ((locGlobalEnd.x - locGlobalInicio.x) * -1) : locGlobalInicio.x}
                width={(locGlobalEnd.x - locGlobalInicio.x) < 0 ? (locGlobalEnd.x - locGlobalInicio.x) * -1 : (locGlobalEnd.x - locGlobalInicio.x)}
                height={(locGlobalEnd.y - locGlobalInicio.y) < 0 ? (locGlobalEnd.y - locGlobalInicio.y) * -1 : (locGlobalEnd.y - locGlobalInicio.y)} fill='rgba(25,255,230,0.1)'
                stroke='blue'></rect> : <></>}
            </g>
          </svg>
        </div>
        <div onMouseMove={(e) => {

          /*   if (bol) {
              const v = e.movementY
              if (v < 0) {
                reduceY = reduceY + 0.02
                updateSchemePath()
              }
              if (v > 0) {
                reduceY = reduceY - 0.02
                updateSchemePath()
              }
            } */
        }} ref={ref2} style={{ marginLeft: '-100px', marginTop: 32, maxHeight: hUi - 64 }} className='backdrop-blur-lg bg-gray-700/10 w-[100px] overflow-hidden'>
          <svg className='hover:cursor-ns-resize' width="100" height="25000" fill="none" xmlnsXlink="http://www.w3.org/1999/xlink">
            <g>
              {sidebar}
              {/* {linePrice} */}
            </g>
          </svg>
        </div>
      </div>
      <div className='relative'>
        <div ref={ref3} style={{ marginTop: '-32px', minHeight: 32, minWidth: wUi }} className='backdrop-blur-lg  bg-gray-700/10 overflow-hidden'>
          <svg className='hover:cursor-pointer' width="25000" height="32" fill="none" xmlnsXlink="http://www.w3.org/1999/xlink">
            <g>
              {sidebarX}
            </g>
          </svg>
        </div>
      </div>
    </div>

  </>;
}

export default Home;